/* eslint-disable react/prop-types */
import React, { Component } from 'react';
import {
  RichContentEditor,
  RichContentEditorModal,
  EditorState,
} from 'wix-rich-content-editor';
import { ContentState, convertFromHTML } from 'wix-rich-content-editor-common';
import ReactModal from 'react-modal';
import ModalsMap from './ModalsMap';

const modalStyleDefaults = {
  content: {
    top: '50%',
    left: '50%',
    right: 'auto',
    bottom: 'auto',
    marginRight: '-50%',
    transform: 'translate(-50%, -50%)',
  },
};
const anchorTarget = '_blank';
const relValue = 'noopener';
const shouldMultiSelectImages = false;

class Editor extends Component {
  constructor(props, context) {
    super(props, context);
    const blocksFromHTML = convertFromHTML(this.props.value || ' ');
    const contentState = ContentState.createFromBlockArray(
      blocksFromHTML.contentBlocks,
      blocksFromHTML.entityMap,
    );
    this.state = {
      showModal: false,
      modalProps: false,
      modalStyles: {},
      editorState: EditorState.createWithContent(contentState),
    };
  }

  onChange = (value) => {
    this.setState({ editorState: value });
    console.log(value);
  };

  helpers = {
    openModal: (data) => {
      const { modalStyles, ...modalProps } = data;
      this.setState({
        showModal: true,
        modalProps,
        modalStyles,
      });
    },
    closeModal: () => {
      this.setState({
        showModal: false,
        modalProps: null,
        modalStyles: null,
      });
    },
  };

  render() {
    const {
      forID,
      classNameWrapper,
      setActiveStyle,
      setInactiveStyle,
    } = this.props;
    const { modalStyles, editorState, modalProps } = this.state;
    return (
      <div
        className={`rich-text-editor ${classNameWrapper}`}
        style={{ maxHeight: '80vh', paddingLeft: 60, overflow: 'auto' }}
      >
        <RichContentEditor
          placeholder="Content"
          helpers={this.helpers}
          // plugins={this.plugins}
          // // config={Plugins.getConfig(additionalConfig)}
          // config={this.pluginsConfig}
          editorKey={forID}
          onChange={this.onChange}
          editorState={editorState}
          onFocus={setActiveStyle}
          onBlur={setInactiveStyle}
        />

        <ReactModal
          isOpen={this.state.showModal}
          contentLabel="External Modal Example"
          style={modalStyles}
          role="dialog"
          onRequestClose={this.helpers.closeModal}
        >
          <RichContentEditorModal modalsMap={ModalsMap} {...modalProps} />
        </ReactModal>
      </div>
    );
  }
}

export default Editor;
